<template lang="pug">
#about 
  .content-container(
    :style="{ opacity: showEl ? 1 : 0, transform: showEl ? 'translateY(0px)' : 'translateY(-20px)' }"
  )
    .heading 
      .heading-row
        p About
        .heading-row-line
      .heading-column
        .heading-column-line
        .inner-content
          .flex-content
            .left-content
              .profile-photo
                img(src="../assets/profile-2-tiny.jpg")
            .right-content
              p.title.highlight-text Tan Wei Shen
              p.title Full Stack Developer

          .lower-content
            .description
              p.sub-text I'm a software engineer based in Selangor, Malaysia.
              p.sub-text I use digital tools to solve technical problems. Here are some of the tools that I use:
              .description
                .skill-item(
                  v-for="(skill, index) in skills",
                  :key="'skill-' + index",
                  :class="{ 'skill-item-end': index === skills.length - 1 }"
                )
                  .skill-item-icon
                    svg.bi.bi-caret-right-fill(
                      width="16px",
                      height="16px",
                      viewBox="0 0 16 16",
                      fill="currentColor",
                      xmlns="http://www.w3.org/2000/svg"
                    )
                      path(
                        d="M12.14 8.753l-5.482 4.796c-.646.566-1.658.106-1.658-.753V3.204a1 1 0 0 1 1.659-.753l5.48 4.796a1 1 0 0 1 0 1.506z"
                      )
                  .skill-item-text
                    p {{ skill }}
</template>

<script>
export default {
  data() {
    return {
      skills: [
        "Javascript, Python, HTML, CSS",
        "VueJs",
        "ExpressJs",
        "Postgres/MySQL",
        "Firebase",
      ],
      showEl: false,
    };
  },
  mounted() {
    // eslint-disable-next-line
    const observer = new IntersectionObserver((entries) => {
      if (entries[0].intersectionRatio > 0) {
        if (!this.showEl) {
          setTimeout(() => {
            this.showEl = true;
          }, 500);
        }
        // console.log("Cross about");
      }
    });
    observer.observe(document.querySelector("#about"));
    this.$store.state.intersectionObserver.observe(
      document.querySelector("#about")
    );
  },
};
</script>

<style lang="sass" scoped>
#about
  width: 100%
  padding-bottom: 100px
  padding-top: 200px
  position: relative

.content-container
  position: relative
  width: 100%
  max-width: 1024px
  margin: auto
  transition: all 1s ease

.inner-content
  padding: 20px 20px 0px 100px
.flex-content
  display: flex
  justify-content: flex-start
  align-items: center

.left-content
  text-align: left
  margin-right: 20px

.profile-photo
  width: 300px
  height: 300px
  border-radius: 50%
  overflow: hidden
  position: relative
  img
    width: 600px
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)

.title
  font-size: 42px

.highlight-text
  // color: #87ceeb
  color: #30DEFF
  // color: #23A0D3
  font-weight: bold

.sub-text
  font-size: 20px

.description
  padding-top: 20px
  text-align: left

.skill-item
  display: flex
  align-items: center
  padding: 5px

.skill-item-end
  display: flex
  align-items: center
  padding-bottom: 0px

.skill-item-icon
  margin-right: 20px
  width: 16px
  height: 16px

.skill-item-text
  p
    font-size: 16px

.right-content
  text-align: left

.lower-content
  width: 100%
  display: flex
  justify-content: center

.heading
  text-align: left
  display: flex
  align-items: flex-start
  flex-direction: column
  .heading-row
    display: flex
    align-items: center
    p
      color: #30DEFF
      font-size: 42px
      font-weight: bold
    .heading-row-line
      width: 400px
      height: 1px
      flex-grow: 1
      background-color: #30DEFF
      margin-left: 20px
  .heading-column
    display: flex
    align-items: flex-start
    flex-grow: 1
    .heading-column-line
      width: 1px
      flex-grow: 1
      background-color: #30DEFF
      margin-top: 10px
      margin-left: 20px
      height: 400px
</style>
